trong[data-v-622eb73e] {\r\n\tcolor: var(--color-main-text);\n}\n.debt-info-item span[data-v-622eb73e] {\r\n\tcolor: var(--color-text-maxcontrast);\n}\n.form-row[data-v-622eb73e] {\r\n\tdisplay: grid;\r\n\tgrid-template-columns: 1fr 1fr;\r\n\tgap: 16px;\n}\n.form-group[data-v-622eb73e] {\r\n\tdisplay: flex;\r\n\tflex-direction: column;\r\n\tgap: 8px;\n}\n.form-label[data-v-622eb73e] {\r\n\tfont-size: 14px;\r\n\tfont-weight: 500;\r\n\tcolor: var(--color-main-text);\n}\n.form-control[data-v-622eb73e] {\r\n\tpadding: 8px 12px;\r\n\tborder: 1px solid var(--color-border);\r\n\tborder-radius: var(--border-radius-element);\r\n\tbackground-color: var(--color-background-dark);\r\n\tcolor: var(--color-main-text);\r\n\tfont-size: 14px;\r\n\tfont-family: inherit;\n}\n.form-control[data-v-622eb73e]:focus {\r\n\toutline: none;\r\n\tborder-color: var(--color-primary-element);\n}\n.form-control[data-v-622eb73e]::placeholder {\r\n\tcolor: var(--color-text-maxcontrast);\n}\ntextarea.form-control[data-v-622eb73e] {\r\n\tresize: vertical;\r\n\tmin-height: 80px;\n}\n.form-hint[data-v-622eb73e] {\r\n\tfont-size: 12px;\r\n\tcolor: var(--color-text-maxcontrast);\r\n\tmargin-top: 4px;\n}\n.form-actions[data-v-622eb73e] {\r\n\tdisplay: flex;\r\n\tjustify-content: flex-end;\r\n\tgap: 12px;\r\n\tmargin-top: 8px;\r\n\tpadding-top: 16px;\r\n\tborder-top: 1px solid var(--color-border);\n}\n.loading-icon[data-v-622eb73e] {\r\n\tanimation: spin-622eb73e 1s linear infinite;\n}\n@keyframes spin-622eb73e {\nfrom {\r\n\t\ttransform: rotate(0deg);\n}\nto {\r\n\t\ttransform: rotate(360deg);\n}\n}\r\n", "",{"version":3,"sources":["webpack://./src/components/DebtPaymentModal.vue"],"names":[],"mappings":";AA4QA;CACC,eAAe;CACf,MAAM;CACN,OAAO;CACP,QAAQ;CACR,SAAS;CACT,oCAAoC;CACpC,aAAa;CACb,mBAAmB;CACnB,uBAAuB;CACvB,cAAc;CACd,aAAa;AACd;AAEA;CACC,8CAA8C;CAC9C,6CAA6C;CAC7C,yCAAyC;CACzC,gBAAgB;CAChB,WAAW;CACX,gBAAgB;CAChB,gBAAgB;CAChB,aAAa;CACb,sBAAsB;AACvB;AAEA;CACC,aAAa;CACb,mBAAmB;CACnB,8BAA8B;CAC9B,aAAa;CACb,